.nba-wrapper {
	max-width: 1170px;
	min-height:600px;
	margin:17px auto 40px auto;
	padding:17px 0 0 0;
	background: #fafafa;
	border-radius: 5px;
	-webkit-box-shadow: 0 5px 30px -5px #000;
	-moz-box-shadow: 0 5px 30px -5px #000;
	box-shadow: 0 5px 30px -5px #000;
	overflow:hidden;
}

@import './scss/main.scss';